@import '../../../../assets/styles/variables';

.sidebar {
  width: $sidebarWidth;
  border: 1px solid #e2e6eb;
  border-radius: 10px;
  padding: 8px;
  box-shadow: 0 2px 8px rgba(19, 44, 68, 0.12);
  margin-left: 12px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px
}

.tool {
  height: 24px;
  width: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #e2e6eb;
  color: black;
  border-radius: 25%;
  cursor: pointer;
  flex-basis: calc(50% - 4px);

  &:hover {
    box-shadow: 0 2px 8px rgba(19, 44, 68, 0.12);
    border-color: #64b496;
    color: #64b496;
  }
}

.selected {
  color: #64b496;
  border-color: #64b496;
  box-shadow: 0 2px 8px rgba(19, 44, 68, 0.12);
}

.disabled {
  cursor: default;

  &:hover {
    border-color: #e2e6eb;
    box-shadow: unset;
    color: black;
  }
}
