@import '../../../../assets/styles/variables';

.wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: $zIndexFilePicker;
  background-color: #e2e6eb;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hidden {
  display: none;
}

.title {
  cursor: pointer;
  color: #6d849b;

  &:hover {
    color: #132c44;
  }
}
