@import '../../../../assets/styles/variables';

.editor {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.body {
  height: 100%;
  width: 100%;
  display: flex;
  margin-bottom: 24px;
}

.content {
  width: calc(100% - $sidebarWidth);
  height: 100%;
  margin: 0 20px;
  position: relative;
  display: flex;
  flex-direction: column;
}

.info {
  border: 1px solid #e2e6eb;
  border-radius: 10px;
  margin-bottom: 10px;
  height: 60px;
  display: flex;
  gap: 16px;
  align-items: center;
  padding: 12px 16px;
}

.selectedColor {
  border: 4px solid #e2e6eb;
  padding: 4px;
  border-radius: 8px;
}

.pickerDiameter {
  display: flex;
  align-items: center;
}

.select {
  padding: 6px 8px;
  border-radius: 8px;
}

.cross {
  height: 30px;
  width: 30px;
  border: 1px solid #6d849b;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  cursor: pointer;
  color: #6d849b;
  margin-left: auto;

  &:hover {
    border-color: #132c44;
    color: #132c44;
  }
}

.image {
  background-color: #e2e6eb;
  height: 100%;
  width: 100%;
  position: relative;
}

.canvas {
  border: 1px solid #e2e6eb;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: $zIndexCanvas;
  left: 0;
}

.hiddenCanvas {
  z-index: $zIndexHiddenCanvas;
}

.picker {
  cursor: crosshair;
}
